import "core-js/modules/es.error.cause.js";
import DrawerComponents from '@/components/coverComponents/drawerComponents.vue';
import TempateForm from "@/components/optionsForm/templateForm.vue";
import { ref, defineComponent } from 'vue';
import { addUserFormOptions } from './options.js';
export default defineComponent({
  components: {
    DrawerComponents,
    TempateForm
  },
  props: {
    value: Boolean
  },
  emits: ['update:modelValue'],

  setup(props, {
    emit
  }) {
    let showDrawer = ref(false),
        formData = ref({
      role: []
    }),
        formDOM = ref();
    const {
      rules,
      items: optionsItem,
      ...rest
    } = addUserFormOptions;
    const formOptions = { ...rest,
      rules: { ...rules,
        confirmPasswrod: {
          required: true,
          validator: (rule, value, callback) => {
            formDOM.value.formData.passwrod && formDOM.value.formData.passwrod === value ? callback() : callback(new Error('两次密码不一致'));
          }
        }
      },
      items: [...optionsItem, {
        controlType: "select",
        keyName: "role",
        multiple: true,
        label: "角色",
        placeholder: "可多选角色",
        options: [{
          value: 1,
          label: "超级管理员"
        }, {
          value: 2,
          label: "管理员"
        }, {
          value: 3,
          label: "开发人员"
        }]
      }]
    };

    const confirmSubmit = () => {
      formDOM.value?.validate(result => {
        emit('update:modelValue', false);
      });
    };

    const closedModel = () => {
      formDOM.value?.resetFields();
      emit('update:modelValue', false);
    };

    return {
      showDrawer,
      formData,
      formOptions,
      formDOM,
      confirmSubmit,
      closedModel
    };
  }

});